<template>
  <div class="hello">
    {{msg}}
    <button v-on:click="exit">退出</button>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: '主页',
    }
  },
  methods:{
    exit(){
      this.$cookies.remove('loginusername')
      this.$router.push('/login')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  button{display:block; width:200px; height:40px; line-height: 40px; margin:0 auto; border:none; background-color:#41b883; color:#fff; font-size:16px; margin-bottom:5px;}
</style>



