<template>
    <div id="Register">
      <div class="register-wrap">
        <h1>注册</h1>
        <input type="text" placeholder="请输入用户名" v-model="newUsername">
        <p class="yq">大小写字母 数字 '-' '_'，长度3~20</p>
        <input type="password" placeholder="请输入密码" v-model="newPassword1">
        <p class="yq">大小写字母 数字 长度6~12</p>
        <input type="password" placeholder="请输入再次密码" v-model="newPassword2">
        <p class="ts"v-show="showTishi">{{tishi}}</p>
        <button v-on:click="register">注册</button>
        <span v-on:click="ToLogin">已有账号？马上登录</span>
      </div>
    </div>
</template>

<script>
    export default {
        name: "Register",
        data(){
          return{
            showTishi: false,
            tishi: '',
            newUsername: '',
            newPassword1: '',
            newPassword2: ''
          }
        },
        methods:{
          register(){
            if(this.newUsername==""||this.newPassword1==""||this.newPassword2==""){
              alert("请输入输入完整信息")
            }
            else if(this.newPassword1!=this.newPassword2){
              this.tishi="两次密码不一致"
              this.showTishi=true
              this.newPassword2=''
            }
            else {
              let data={'username':this.newUsername,'password':this.newPassword1}
              this.$axios.post('https://afwy5h.toutiao15.com/register_use',data)
                .then((res)=>{console.log(res)
                  if(res.data.status==0){
                    alert("注册成功")
                    this.$router.push('/login')
                  }
                  else if(res.data.status==1){
                    this.tishi="用户名或密码长度不合法"
                    this.showTishi=true
                    this.newUsername=''
                    this.newPassword1=''
                    this.newPassword2=''
                  }
                  else if(res.data.status==2){
                    this.tishi="用户名或密码存在不合法字符"
                    this.showTishi=true
                    this.newUsername=''
                    this.newPassword1=''
                    this.newPassword2=''
                  }
                  else if(res.data.status==3){
                    this.tishi="用户名已存在"
                    this.showTishi=true
                    this.newUsername=''
                    this.newPassword1=''
                    this.newPassword2=''
                  }
                })
            }
          },
          ToLogin(){
            this.$router.push('/login')
          }
        }
    }
</script>

<style scoped>
  .register-wrap{text-align:center;}
  input{display:block; width:250px; height:40px; line-height:40px; margin:0 auto; margin-bottom: 10px; outline:none; border:1px solid #888; padding:10px; box-sizing:border-box;}
  .yq{color:grey;font-size: 1px}
  button{display:block; width:200px; height:40px; line-height: 40px; margin:0 auto; border:none; background-color:#41b883; color:#fff; font-size:16px; margin-bottom:5px;}
  span{cursor:pointer;}
  span:hover{color:#41b883;}
</style>



