<template>
    <div id="Login">
      <div class="login-wrap">
        <h1>登录</h1>
        <p v-show="showTishi">{{tishi}}</p>
        <input type="text" placeholder="请输入用户名" v-model="username">
        <input type="password" placeholder="请输入密码" v-model="password">
        <button v-on:click="login">登录</button>
        <span v-on:click="ToRegister">没有账号？马上注册</span>
      </div>
    </div>
</template>

<script>
    export default {
        name: "Login",
        data(){
          return{
            showTishi: false,
            tishi: '',
            username: '',
            password: ''
          }
        },
        created() {
          this.$axios.get("")
        },
        methods:{
          ToRegister(){
            this.$router.push('/register')
          },
          login(){
            if(this.username==""||this.password==""){
              alert("请输入用户名和密码")
            }
            else{
              let data={'username':this.username,'password':this.password}
              this.$axios.post('https://afwy5h.toutiao15.com/login_use',data)
                .then((res)=>{console.log(res)
                  if (res.data.status==1){
                    this.tishi="用户名或密码错误"
                    this.showTishi=true
                    this.username=""
                    this.password=""
                  }
                  else if(res.data.status==0){
                    this.$cookies.set('loginusername',this.username)
                    alert(("登录成功"))
                    this.$router.push('/')
                  }
                })
              }
            }
        }
    }
</script>

<style>
  .login-wrap{text-align:center;}
  input{display:block; width:250px; height:40px; line-height:40px; margin:0 auto; margin-bottom: 10px; outline:none; border:1px solid #888; padding:10px; box-sizing:border-box;}
  p{color:red;}
  button{display:block; width:200px; height:40px; line-height: 40px; margin:0 auto; border:none; background-color:#41b883; color:#fff; font-size:16px; margin-bottom:5px;}
  span{cursor:pointer;}
  span:hover{color:#41b883;}
</style>



