<template>
    <div id="ChangePassword">
      <div class="changepassword-wrap">
        <h1>修改密码</h1>
        <input type="password" placeholder="请输入原密码" v-model="oldpassword">
        <input type="password" placeholder="请输入新密码" v-model="newpassword1">
        <p>大小写字母 数字 长度6~12</p>
        <input type="password" placeholder="请重新输入新密码" v-model="newpassword2">
        <p v-show="showTishi">{{tishi}}</p>
        <div class="button-warp">
          <button v-on:click="change">确认</button>
          <button v-on:click="back">返回</button>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "ChangePassword",
        data() {
          return {
            showTishi: false,
            tishi: '',
            oldpassword: '',
            newpassword1: '',
            newpassword2: ''
          }
        },
        methods:{
          back(){
            this.$router.push('/')
          },
          change(){
            if(this.oldpassword==""||this.newpassword1==""||this.newpassword2=="") {
              alert("请输入完整信息")
            }
            else if(this.newpassword1!=this.newpassword2) {
              this.tishi="两次新输入密码不一致"
              this.showTishi=true
              this.newpassword2=""
            }
            else {
              let data={'username':this.$cookies.get('loginusername'),'password':this.oldpassword,'new_password':this.newpassword2}
              this.$axios.post('https://afwy5h.toutiao15.com/change_password_use',data)
                .then((res)=>{console.log(res)
                  if(res.data.status===1){
                    this.tishi='修改失败'
                    this.showTishi=true
                    this.oldpassword=''
                    this.newpassword1=''
                    this.newpassword2=''
                  }
                  else if(res.data.status===0){
                    alert("修改成功")
                    this.$router.push('/')
                  }
                })
            }
          }
        }
    }
</script>

<style scoped>
  .changepassword-wrap{text-align:center;}
  input{display:block; width:250px; height:40px; line-height:40px; margin:0 auto; margin-bottom: 10px; outline:none; border:1px solid #888; padding:10px; box-sizing:border-box;}
  p{color:red;}
  button{width:123px; height:40px; line-height: 40px; margin:0 auto; border:none; background-color:#41b883; color:#fff; font-size:16px; margin-bottom:5px;}
  span{cursor:pointer;}
  span:hover{color:#41b883;}
  .button-warp{display: inline-block;}
</style>



